import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService } from '../login.service';
import { AbstractControl, FormBuilder } from '@angular/forms';
import { IAuth, IToken } from '@shlomit/shared/interfaces';
import { FormBaseDirective } from '@shlomit/ui';
import { CustomValidators } from '../../../../../ui/src/lib/form/error/error.validator';
 
@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss'],
})
export class LoginFormComponent
  extends FormBaseDirective<IAuth>
  implements OnInit {
  constructor(
    private loginService: LoginService,
    private route: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder // private CustomValidators: MultilingualValidators
  ) {
    super(
      fb.group({
        persId: [
          '034235382',
          [
            CustomValidators.required,
            CustomValidators.israelyIdNumber,
            // CustomValidators.validateFirstLastCharacters,
            // CustomValidators.validateCharacters,

            CustomValidators.minLength(9),
            // CustomValidators.maxLength(9),
            // CustomValidators.oneSpaceBetweenWords,
          ],
        ],
      })
    );
  }

  get persId(): AbstractControl {
    return this.form.get('persId');
  }

  ngOnInit() {}
  submit() {
    if (this.form.valid) {
      this.loginService.login(this.form.value).subscribe((data:IToken) => {
        if (data.isSuccess) {
          this.loginService.doLogin(data);
          this.loginService.data.hashMobilePhone = data.hashMobilePhone
          this.router.navigate(['sms'], { relativeTo: this.route.parent });
        } else {
          this.error = data['error'];
        }
      });
    }
  }
}
