import { Injectable } from '@angular/core';
import { AuthService } from '@shlomit/auth/authentication';
import { HttpClient } from '@angular/common/http';
import { AUTH, IToken } from '@shlomit/shared/interfaces';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class LoginService {
  onLogin = new Subject();
  constructor(
    private authService: AuthService,
    private httpClient: HttpClient
  ) {
    this.onLogin.subscribe((data: IToken) => {
      this.authService.tokenData = data;
    });
  }
  data = {
    persId: '034235382',
    hashMobilePhone: '',
    tempCode: '1234',
  };

  token(data: { persId: string; hashMobilePhone: string }) {
    return this.authService.getTokenWithSsmCode(data);

    // return this.httpClient.post(AUTH.TOKEN(), data);
  }

  login(data: any) {
    return this.httpClient.post(AUTH.LOGIN(), data);
  }
  doLogin(data: IToken) {
    this.authService.tokenData = data;

    this.onLogin.next(data);
  }
}

