import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { LoginService } from '../login.service';
import { FormBaseDirective } from '@shlomit/ui';
import { IAuth } from '@shlomit/shared/interfaces';
import { FormBuilder, AbstractControl } from '@angular/forms';
import { CustomValidators } from 'libs/ui/src/lib/form/error/error.validator';
import { IToken } from '../../../../../shared/interfaces/src/lib/auth/token';

@Component({
  selector: 'app-sms',
  templateUrl: './sms.component.html',
  styleUrls: ['./sms.component.scss'],
})
export class SmsComponent extends FormBaseDirective<IAuth> implements OnInit {
  get hashMobilePhone() {
    return this.loginService.data.hashMobilePhone;
  }
  constructor(
    private loginService: LoginService,

    private route: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder // private CustomValidators: MultilingualValidators
  ) {
    super(
      fb.group({
        tempCode: [
          '1234',
          [
            CustomValidators.required,
            // CustomValidators.allSpaces,
            // CustomValidators.validateFirstLastCharacters,
            // CustomValidators.validateCharacters,

            CustomValidators.minLength(4),
            // CustomValidators.maxLength(9),
            // CustomValidators.oneSpaceBetweenWords,
          ],
        ],
        persId: [],
      })
    );

    if (!this.loginService.data.hashMobilePhone) {
      this.router.navigate(['/auth'], {
        relativeTo: this.route.parent.root,
      });
    }
  }

  get persId(): string {
    return this.loginService.data.persId;
  }

  get tempCode(): AbstractControl {
    return this.form.get('tempCode');
  }
  ngOnInit(): void {}
  submit() {
    if (this.form.valid) {
      this.error = null;
      this.loginService.data.tempCode = this.form.value['tempCode'];

      this.loginService
        .token(this.loginService.data)
        .subscribe((data: IToken) => {
          if (data.isSuccess) {
            this.loginService.doLogin(data);

            this.router.navigate(['/dashboard'], {
              relativeTo: this.route.parent.root,
            });
          } else {
            this.error = data['error'];
          }
        });
    }
  }

  // this.loginService.token(this.loginService.data).subscribe((data) => {
  //   if (data['isSuccess']) {
  //     this.router.navigate(['/dashboard'], {
  //       relativeTo: this.route.parent.root,
  //     });
  //   }
  // });
  // }

  resendCode(): void {}
}
